.goat-management {
  padding: 20px;
  max-width: 1400px;
  margin: 0 auto;
}

.goat-mgmt-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  flex-wrap: wrap;
  gap: 20px;
}

.goat-mgmt-header h1 {
  color: #27ae60;
  margin: 0;
  font-size: 32px;
}

.goat-mgmt-loading {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 500px;
  font-size: 18px;
  color: #666;
}

.error-alert {
  background-color: #f8d7da;
  color: #721c24;
  padding: 15px;
  border-radius: 5px;
  margin-bottom: 20px;
  border: 1px solid #f5c6cb;
}

.success-alert {
  background-color: #d4edda;
  color: #155724;
  padding: 15px;
  border-radius: 5px;
  margin-bottom: 20px;
  border: 1px solid #c3e6cb;
}

.search-section {
  margin-bottom: 30px;
}

.search-input {
  width: 100%;
  max-width: 400px;
  padding: 12px 15px;
  border: 2px solid #ddd;
  border-radius: 5px;
  font-size: 14px;
  transition: border-color 0.3s;
}

.search-input:focus {
  outline: none;
  border-color: #27ae60;
  box-shadow: 0 0 5px rgba(39, 174, 96, 0.3);
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  overflow-y: auto;
  padding: 20px 0;
}

.modal-content {
  background: white;
  border-radius: 10px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.3);
  max-width: 600px;
  width: 90%;
  max-height: 90vh;
  overflow-y: auto;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid #eee;
}

.modal-header h2 {
  margin: 0;
  color: #27ae60;
  font-size: 24px;
}

.close-btn {
  background: none;
  border: none;
  font-size: 32px;
  cursor: pointer;
  color: #999;
  padding: 0;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.close-btn:hover {
  color: #333;
}

.goat-form {
  padding: 20px;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 15px;
  margin-bottom: 15px;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group.full-width {
  grid-column: 1 / -1;
}

.form-group label {
  margin-bottom: 8px;
  color: #333;
  font-weight: 600;
  font-size: 14px;
}

.form-group input,
.form-group select,
.form-group textarea {
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 14px;
  font-family: inherit;
  transition: border-color 0.3s;
  box-sizing: border-box;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #27ae60;
  box-shadow: 0 0 5px rgba(39, 174, 96, 0.3);
}

.form-actions {
  display: flex;
  gap: 10px;
  justify-content: flex-end;
  margin-top: 25px;
  padding-top: 20px;
  border-top: 1px solid #eee;
}

.confirm-modal {
  max-width: 400px;
  text-align: center;
  padding: 30px;
}

.confirm-modal h2 {
  color: #e74c3c;
  margin-top: 0;
  margin-bottom: 15px;
}

.confirm-modal p {
  color: #666;
  margin: 10px 0;
  line-height: 1.6;
}

.confirm-modal .warning {
  color: #e74c3c;
  font-weight: 600;
  margin-top: 15px;
}

.modal-actions {
  display: flex;
  gap: 10px;
  justify-content: center;
  margin-top: 25px;
}

.goats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
}

.goat-card {
  background: white;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  overflow: hidden;
  transition: all 0.3s;
  display: flex;
  flex-direction: column;
}

.goat-card:hover {
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  transform: translateY(-5px);
}

.goat-card-header {
  background: linear-gradient(135deg, #f9f9f9 0%, #ffffff 100%);
  padding: 15px;
  border-bottom: 2px solid #f0f0f0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.goat-card-title {
  display: flex;
  align-items: center;
  gap: 10px;
}

.gender-icon {
  font-size: 24px;
}

.gender-icon[data-gender="male"] {
  color: #3498db;
}

.gender-icon[data-gender="female"] {
  color: #e91e63;
}

.goat-name {
  font-size: 18px;
  font-weight: 700;
  color: #333;
}

.goat-tag {
  color: #7f8c8d;
  font-size: 12px;
  font-weight: 600;
  background: #f5f5f5;
  padding: 4px 8px;
  border-radius: 3px;
}

.goat-card-body {
  padding: 15px;
  flex: 1;
}

.info-row {
  display: flex;
  justify-content: space-between;
  padding: 8px 0;
  border-bottom: 1px solid #f0f0f0;
  font-size: 14px;
}

.info-row:last-of-type {
  border-bottom: none;
}

.info-row .label {
  color: #7f8c8d;
  font-weight: 600;
}

.info-row .value {
  color: #333;
  font-weight: 500;
}

.badges-row {
  display: flex;
  gap: 8px;
  margin-top: 12px;
  flex-wrap: wrap;
}

.badge {
  padding: 4px 10px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: 700;
  color: white;
}

.badge.health-healthy {
  background-color: #27ae60;
}

.badge.health-sick {
  background-color: #e74c3c;
}

.badge.health-pregnant {
  background-color: #f39c12;
}

.badge.health-injured {
  background-color: #e67e22;
}

.badge.breeding-breeding {
  background-color: #2ecc71;
}

.badge.breeding-non-breeding {
  background-color: #95a5a6;
}

.badge.breeding-retired {
  background-color: #34495e;
}

.goat-card-actions {
  display: flex;
  gap: 10px;
  padding: 12px 15px;
  border-top: 1px solid #f0f0f0;
  justify-content: center;
}

.btn-icon {
  background: none;
  border: 1px solid #ddd;
  padding: 8px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 18px;
  flex: 1;
  transition: all 0.3s;
}

.btn-edit {
  color: #3498db;
  border-color: #3498db;
}

.btn-edit:hover {
  background-color: #3498db;
  color: white;
}

.btn-delete {
  color: #e74c3c;
  border-color: #e74c3c;
}

.btn-delete:hover {
  background-color: #e74c3c;
  color: white;
}

.no-goats {
  grid-column: 1 / -1;
  background: white;
  padding: 60px 20px;
  text-align: center;
  border-radius: 10px;
  color: #7f8c8d;
  font-size: 16px;
}

.btn {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: 600;
  font-size: 14px;
  transition: all 0.3s;
}

.btn-primary {
  background-color: #27ae60;
  color: white;
}

.btn-primary:hover {
  background-color: #229954;
  transform: translateY(-2px);
}

.btn-secondary {
  background-color: #95a5a6;
  color: white;
}

.btn-secondary:hover {
  background-color: #7f8c8d;
}

.btn-danger {
  background-color: #e74c3c;
  color: white;
}

.btn-danger:hover {
  background-color: #c0392b;
}

/* Parent Management Styles */
.parent-badge {
  display: inline-block;
  background-color: #e8f4f8;
  border: 1px solid #27ae60;
  color: #27ae60;
  padding: 5px 12px;
  border-radius: 4px;
  font-size: 13px;
  margin: 5px 8px 5px 0;
  font-weight: 500;
}

.parents-info {
  margin-top: 10px;
  padding-top: 10px;
  border-top: 1px solid #eee;
}

.parents-info-label {
  font-weight: 600;
  color: #555;
  font-size: 12px;
  text-transform: uppercase;
  margin-bottom: 8px;
  display: block;
}

.btn-parents {
  background-color: #3498db;
  color: white;
  border: none;
  padding: 8px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 13px;
  font-weight: 500;
  margin-right: 8px;
  transition: background-color 0.3s ease;
}

.btn-parents:hover {
  background-color: #2980b9;
}

/* Parent Modal and Form Styles */
.parent-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.parent-form-group {
  display: flex;
  flex-direction: column;
}

.parent-form-group label {
  margin-bottom: 8px;
  font-weight: 600;
  color: #333;
  font-size: 14px;
}

.parent-form-group select,
.parent-form-group input {
  padding: 10px;
  border: 2px solid #ddd;
  border-radius: 4px;
  font-size: 14px;
  transition: border-color 0.3s ease;
}

.parent-form-group select:focus,
.parent-form-group input:focus {
  outline: none;
  border-color: #27ae60;
}

@media (max-width: 768px) {
  .goat-mgmt-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .form-row {
    grid-template-columns: 1fr;
  }

  .goats-grid {
    grid-template-columns: 1fr;
  }

  .modal-content {
    width: 95%;
  }
}
